import * as React from 'react';
import { Button } from 'semantic-ui-react';
import ActionButtonHoc from '@music/hocs/ActionButtonHoc';
import { IMix } from '@music/interfaces';
import { IButtonVariant } from '@music/components/stateless/PlayButtonVariants/ButtonVariant1';

function ButtonVariant3({ action, isPlaying, isLoading, mix = {} as IMix }: IButtonVariant) {
  return (
    <>
      <Button
        circular
        size="mini"
        onClick={() => action()}
        loading={!isPlaying && isLoading}
        icon={(isPlaying ? 'pause' : 'play')}
      />
    </>
  );
}

export default ActionButtonHoc(ButtonVariant3);



