import * as React from 'react';
import { connect } from 'react-redux';
import { withRouter } from 'react-router-dom';
import SidebarComponent from '../../layouts/Sidebar';
import { Segment, Card, Image, Header } from 'semantic-ui-react';
import SeriesListHoc from '@music/hocs/series/ListHoc';
import config from '@music/config';
import { seriesModel } from '@music/models';
import { get } from '@music/utils';
import { setMainMix } from '@music/actions/player';
import { prepareMix, addGenresIfNotExists } from '@music/actions/mix';
import { SizeMe } from 'react-sizeme';
import gaService from '@music/_services/gaService';
import { IArtist, IKeyValueObject, IMix, IState, ISeries, IRouteProps, MainMixType } from '@music/interfaces';
import PlayButtonVariant3 from '@music/components/stateless/PlayButtonVariants/ButtonVariant3';
import container from '@music/_services/container';
import EntityCollection from '@music/extensions/entity-collection';

interface ISeriesListProps extends IRouteProps {
  series: ISeries[];
  artists: IArtist[];
  container: EntityCollection;
}

class SeriesList extends React.Component<ISeriesListProps, IKeyValueObject> {

  constructor(props: ISeriesListProps) {
    super(props);
    this.state = {};
  }

  private get container(): EntityCollection {
    return this.props.container;
  }

  public componentDidMount() {
    this.setState((state: IKeyValueObject) => {
        const artists = this.props.artists.reduce((result: any, artist: any) => {
          result[artist.id] = artist;
          return result;
        }, {})
        return {...state, artists};
      }, () => gaService.pageView(`Series List`)
    )
  }

  public render() {
    const { artists } = this.state;
    return !artists ? null : (
      <>
      <SizeMe>
        {({ size }) => (
        <SidebarComponent>
          <Segment style={{height: 'auto'}} className="toptracks-container">
            <Header as='h1'>Series</Header>
            <Card.Group>
            {this.props.series.map(s => (
              <Card fluid={!((size.width || 0) > 603)} centered={!((size.width || 0) > 603)} key={s.id} link>
                <Image onClick={this.go(s)} src={config(`images.${s.slug}.300x300`)} />
                <Card.Content onClick={this.go(s)}>
                  <Card.Header>{s.name}</Card.Header>
                  <Card.Meta>
                    by <strong className='date'>{get(`${s.artistId}.name`, artists)}</strong>
                  </Card.Meta>
                  <Card.Description>{s.about}</Card.Description>
                </Card.Content>
                <Card.Content extra>
                  {((mix: IMix|undefined) => {
                    return (
                      <>
                      {mix &&
                        <PlayButtonVariant3
                          {...this.props}
                          mix={{
                            ...mix,
                            series: s
                          }}
                          service={container.get('mainPlayer')}
                        />
                      }
                      </>
                    )
                  })(this.findMix(s))}
                  {' '}
                  <strong>
                    {s.name} #{s.count}
                  </strong>
                </Card.Content>
              </Card>
            ))}
            </Card.Group>
          </Segment>
        </SidebarComponent>
        )}
        </SizeMe>
      </>
    );
  }

  private findMix(series: ISeries): IMix|undefined {
    const mix = this.container.where({
      seriesId: series.id,
      count: series.count
    }).first();

    console.log('findMix', mix, series);
    return mix ? {...mix, series} : mix;
  }

  private go(series: ISeries): () => any {
    return () => {
      this.props.history.push(seriesModel.newInstance(series).url());
    }
  }
}

const mapDispatchToProps = (dispatch: CallableFunction) => {
  return {
    setMainMix: (mix: MainMixType) => dispatch(setMainMix({...mix})),
    prepareMix: (mix: IMix, addToContainer: boolean, cb: (mix: IMix) => any) => dispatch(prepareMix(mix, addToContainer, cb)),
    addGenresIfNotExists: (mix: IMix,) => dispatch(addGenresIfNotExists(mix))
  }
};

const mapStateToProps = (state: IState) => {
  return {
    series: state.series.data,
    artists: state.artists.data,
    container: new EntityCollection(state.mixContainer.data)
  }
};

const SeriesListPage = connect(mapStateToProps, mapDispatchToProps)(withRouter(SeriesList as any));

export default SeriesListHoc(SeriesListPage);



